@import "https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap";
* {
  list-style: none;
}

html {
  height: 100%;
  width: 1000px;
  background-color: rgba(46, 87, 107, .4);
  background-image: url("https://78.media.tumblr.com/cae86e76225a25b17332dfc9cf8b1121/tumblr_p7n8kqHMuD1uy4lhuo1_540.png"), url("https://78.media.tumblr.com/66445d34fe560351d474af69ef3f2fb0/tumblr_p7n908E1Jb1uy4lhuo1_1280.png"), url("https://78.media.tumblr.com/8cd0a12b7d9d5ba2c7d26f42c25de99f/tumblr_p7n8kqHMuD1uy4lhuo2_1280.png"), url("https://78.media.tumblr.com/5ecb41b654f4e8878f59445b948ede50/tumblr_p7n8on19cV1uy4lhuo1_1280.png"), url("https://78.media.tumblr.com/28bd9a2522fbf8981d680317ccbf4282/tumblr_p7n8kqHMuD1uy4lhuo3_1280.png");
  background-position: 0 20%, 0 100%, 0, 0 100%, 0 0;
  background-repeat: repeat-x;
  background-size: 2500px, 800px, 500px 200px, 1000px, 400px 260px;
  animation: para 50s linear infinite;
}

@keyframes para {
  100% {
    background-position: -5000px 20%, -800px 95%, 500px, 1000px 100%, 400px 0;
  }
}

:root {
  --color-red: #ec1840;
  --color-purple: #7a18ec;
  --color-white: #fff;
  --color-black-1: #111;
  --color-black-2: #222;
  --color-black-3: #444;
  --speed-normal: .5s;
  --speed-fast: .8s;
}

.main-link {
  width: 180px;
  height: 60px;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-align: center;
  color: var(--color-red);
  trasnition: var(--speed-normal);
  border: 1px solid var(--color-red);
  margin: 20px;
  padding: 20px;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.main-link:hover, .main-link:focus {
  background: var(--color-red) url("https://i.postimg.cc/wBXGXbWN/pixel.png");
  animation: animate var(--speed-fast) steps(8) forwards;
  background-size: 180px;
  border: 1px solid rgba(0, 0, 0, 0);
  transition-delay: .8s;
}

.main-link:last-of-type {
  border: 1px solid var(--color-purple);
}

.main-link:last-of-type:hover, .main-link:last-of-type:focus {
  background: var(--color-purple) url("https://i.postimg.cc/FzBWFtKM/pixel2.png");
}

@keyframes animate {
  0% {
    background-position-y: 0;
  }

  100% {
    background-position-y: -480px;
  }
}

